body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.annotation-area {
  border-radius: 1.8px;
}

.page-div {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 2px;
}

.viewer-wrapper {
  background-color: #dddddd
}

.document-title {
  width: 66vw;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}

.navbar-title {
  width: 50vw;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}

.popup-list .am-list-header {
  padding-top: 10px;
  padding-bottom: 10px;
  margin-left: auto;
  margin-right: auto;
  width: 88vw;
  overflow: hidden;
  text-overflow: ellipsis;
}

.am-tabs-bottom > .am-tabs-tab-bar-wrap {
  position: absolute;
  width: 100%;
  height: 50px;
  bottom: 0;
  left: 0;
}

.am-tabs-bottom > .am-tabs-content-wrap {
  padding-bottom: 50px;
}

.am-tab-bar-bar-hidden-bottom {
  display: none;
}

* {
  -webkit-overflow-scrolling: touch;
}

.grid-tile {
  height: 56.56vw;
  width: 40vw;
  background: #fff;
  border-radius: 3px;
  margin-left: 3px;
  margin-right: 3px;
}

.grid-tile img {
  width: 100%;
}

.grid-tile div {
  border-radius: 0 0 3px 3px;
}

.highlighted-annotation-area {
  border-style: solid;
  border-width: 1px;
}

.annotation-drawer {
  box-shadow:  0px -2px 2px #eee !important;
  max-height: 60vh !important;
}

.comment-text {
  padding: 5px;
}

.comment-text img {
  border-radius: 1.8px;
  max-width: 88vw;
  height: auto;
}

.comment-head {
  box-shadow: 0 4px 2px #eee;
  padding-right: 4px;
  margin-bottom: 4px;
  border-radius: 0 0 10px 10px;
}

.comment-body {
  box-shadow: 0 4px 4px #eee;
  margin: 4px;
  border: 1px solid #eee;
  border-radius: 10px;
}

.menu span {
  display: inline-block;
}

.menu ul {
  padding: 0;
}

.menu li {
  padding: 10px;
}

.menu Button {
  padding: 0;
  padding-right: 10px;
}

#post-annotation-btn {
  height: 24px;
  width: 24px;
  vertical-align: bottom;
  margin-bottom: 16px;
  margin-left: 24px;
  color: #108ee9;
}

#cancel-annotation-btn {
  height: 24px;
  width: 24px;
  vertical-align: bottom;
  margin-bottom: 16px;
  margin-left: 24px;
  color: #7f8c8d;
}

#annotation-being-created {
  z-index: 1;
  background-color: rgba(0, 170, 0, 0.18);
}

.cancel-comment-btn {
  height: 24px;
  width: 24px !important;
  vertical-align: bottom;
  margin-bottom: 16px;
  margin-left: 24px;
  color: #7f8c8d;
}

.commit-comment-btn {
  height: 24px;
  width: 24px !important;
  vertical-align: bottom;
  margin-bottom: 16px;
  margin-left: 24px;
  color: #108ee9;
}

#logo-div {
  height: 160px;
  text-align: center;
}

#logo {
  margin-top: 28px;
  height: 108px;
}

#login-card {
  margin-left: 8vw;
  margin-right: 8vw;
  margin-top: 12vh;
  padding-bottom: 8vh;
}

.social {
  display: flex;
  justify-content: center
}

.social-button {
  width: 66%;
  height: 48px;
}

#google-button {
  background-color: #DD4B39;
  border-color: #DD4B39
}

#facebook-button {
  background-color: #3b5998;
  border-color: #3b5998
}

#nus-button {
  background-color: #FDA428;
  border-color: #FDA428
}

.social-icon {
  color: white;
  height: 28px;
  width: 28px !important;
  position: absolute;
  left: 8%;
}

#submit-icon {
  color: white;
  height: 28px;
  width: 28 !important;
  margin-right: 28px;
}

.social-text {
  color: white;
  font-size: 13px;
  font-weight: 400;
  text-transform: none;
  position: absolute;
  left: 28%;
}

.login-text {
  color: white;
  font-size: 13px;
  font-weight: 400;
  text-transform: none;
}

.input-width {
  width: 69%
}

